import localforage from 'localforage';
localforage.config({
  driver: localforage.LOCALSTORAGE,
  name: 'CMS'
});

export const authenticate = () => {
  return new Promise((resolve, reject) => {
    resolve({ token: 1243 });
  });
};

const logout = () => {
  return new Promise((resolve, reject) => {
    resolve({ token: null });
  });
};

const loadCollection = id => localforage.getItem(id);

const addItem = (id, slug, item) => {
  return loadCollection(id).then(items => {
    const allItems = items || {};
    return localforage.setItem(id, {
      ...allItems,
      [slug]: item
    });
  });
};

const updateItem = addItem;

const removeItem = (id, slug) => {
  return loadCollection(id).then(items => {
    const newCollection = { ...items };
    delete newCollection[slug];
    return localforage.setItem(id, newCollection);
  });
};

export default {
  logout,
  authenticate,
  loadCollection,
  addItem,
  updateItem,
  removeItem
};



