import React, { Component } from 'react';
import './style.css';
import Form from '../form';
import { getSingle } from '../../state/config';
import { getData, update, load } from '../../state/singles';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';

class Single extends Component {
  componentDidMount() {
    const { match } = this.props;
    if (match.params.id) {
      this.props.load(match.params.id);
    }
  }

  render() {
    const { id, slug, update, single, data } = this.props;
    const { name, fields } = single;
    return (
      <div>
        <h1>
          {name}
        </h1>

        <div className="form">
          <Form
            collection={id}
            slug={slug}
            fields={fields}
            onSubmit={data => update(id, data)}
            data={data}
          />
        </div>
      </div>
    );
  }
}

export default withRouter(
  connect(
    (state, { match }) => {
      const { id, slug } = match.params;
      return {
        id,
        slug,
        single: getSingle(state, id),
        data: getData(state, id)
      };
    },
    {
      load,
      update
    }
  )(Single)
);



