import history from '../utils/history';
import { startLoading, finishLoading, errorLoading } from './loading';
import * as config from './config';
import provider from '../providers';
import slugify from 'slugify';

// Constants
export const LOAD = 'COLLECTION_LOAD';
export const UPDATE_ITEM = 'COLLECTION_UPDATE_ITEM';
export const REMOVE_ITEM = 'COLLECTION_REMOVE_ITEM';

// Reducer
export default (state = {}, action) => {
  const { type, slug, collectionID, items, item } = action;
  switch (type) {
    case LOAD:
      return {
        ...state,
        [collectionID]: items
      };
    case UPDATE_ITEM:
      return {
        ...state,
        [collectionID]: {
          ...state[collectionID],
          [slug]: item
        }
      };
    case REMOVE_ITEM:
      const newState = { ...state[collectionID] };
      delete newState[slug];
      return {
        ...state,
        [action.collectionID]: newState
      };
    default:
      return state;
  }
};

// Action creators
const loadCollection = (collectionID, items) => ({
  type: LOAD,
  collectionID,
  items
});

const updateItemInCollection = (collectionID, slug, item) => ({
  type: UPDATE_ITEM,
  collectionID,
  slug,
  item
});

const removeItemInCollection = (collectionID, slug) => ({
  type: REMOVE_ITEM,
  collectionID,
  slug
});

// Actions
export const load = collectionID => (dispatch, getState) => {
  const state = getState();

  // Don't load if we already have the collection
  if (
    state.collections[collectionID] &&
    Object.keys(state.collections[collectionID]).length > 0
  ) {
    return;
  }

  // Grab extra info from state
  const collectionConfig = config.getCollection(state, collectionID);
  const backend = state.config.backend;
  const token = state.auth.user.token;

  dispatch(startLoading());
  provider
    .loadCollection(collectionID, collectionConfig, backend, token)
    .then(items => {
      dispatch(loadCollection(collectionID, items));
      dispatch(finishLoading());
    })
    .catch((err, response) => {
      dispatch(loadCollection(collectionID, {}));
      dispatch(finishLoading());
    });
};

export const addItem = (collectionID, item) => (dispatch, getState) => {
  const state = getState();
  const collectionConfig = config.getCollection(state, collectionID);
  const backend = state.config.backend;
  const token = state.auth.user.token;

  dispatch(startLoading());
  const slug = slugify(item.name).toLowerCase();
  provider
    .addItem(collectionID, slug, item, collectionConfig, backend, token)
    .then(() => {
      dispatch(updateItemInCollection(collectionID, slug, item));
      dispatch(finishLoading());
      history.push(`/collections/${collectionID}`);
    })
    .catch(err => {
      dispatch(errorLoading('An error has occurred'));
    });
};

export const updateItem = (collectionID, slug, item) => (
  dispatch,
  getState
) => {
  const state = getState();
  const collectionConfig = config.getCollection(state, collectionID);
  const backend = state.config.backend;
  const token = state.auth.user.token;

  dispatch(startLoading());
  provider
    .updateItem(collectionID, slug, item, collectionConfig, backend, token)
    .then(() => {
      dispatch(updateItemInCollection(collectionID, slug, item));
      dispatch(finishLoading());
      history.push(`/collections/${collectionID}`);
    })
    .catch(err => {
      dispatch(errorLoading('An error has occurred'));
    });
};

export const removeItem = (collectionID, slug) => (dispatch, getState) => {
  const state = getState();
  const collectionConfig = config.getCollection(state, collectionID);
  const backend = state.config.backend;
  const token = state.auth.user.token;

  dispatch(startLoading());
  provider
    .removeItem(collectionID, slug, collectionConfig, backend, token)
    .then(() => {
      dispatch(removeItemInCollection(collectionID, slug));
      dispatch(finishLoading());
      history.push(`/collections/${collectionID}`);
    })
    .catch(err => {
      dispatch(errorLoading('An error has occurred'));
    });
};

// Selectors
export const getItems = (state, collectionID) =>
  state.collections[collectionID] || {};

export const getItemsByProp = (state, collectionID, prop) => {
  const collection = state.collections[collectionID] || {};
  return Object.keys(collection).map(key => collection[key][prop]);
};

export const getItem = (state, collectionID, slug) => {
  if (!state.collections[collectionID]) {
    return {};
  }
  return state.collections[collectionID][slug] || {};
};



