import React from 'react';
import { connect } from 'react-redux';
import { getCollection } from '../../state/config';

const Image = ({ repository, branch, folder, name, token, ...rest }) =>
  <img
    {...rest}
    src={`https://github.com/${repository}/blob/${branch}/${folder}/${name}?raw=true`}
  />;
export default connect((state, ownProps) => ({
  ...state.config.backend,
  token: state.auth.user.token,
  folder: getCollection(state, ownProps.id).folder
}))(Image);



