// import history from '../utils/history';
import { combineReducers } from 'redux';
import provider from '../providers';
import localforage from 'localforage';
localforage.config({
  driver: localforage.LOCALSTORAGE,
  name: 'auth'
});

// Constants
export const AUTH_REQUEST = 'AUTH_REQUEST';
export const AUTH_SUCCESS = 'AUTH_SUCCESS';
export const AUTH_FAILURE = 'AUTH_FAILURE';
export const LOGOUT = 'AUTH_LOGOUT';

// Reducers
const isAuthorising = (state = false, { type }) => {
  switch (type) {
    case AUTH_REQUEST:
      return true;
    case AUTH_SUCCESS:
    case AUTH_FAILURE:
      return false;
    default:
      return state;
  }
};

const isAuthenticated = (state = false, { type }) => {
  switch (type) {
    case AUTH_SUCCESS:
      return true;
    case LOGOUT:
    case AUTH_FAILURE:
      return false;
    default:
      return state;
  }
};

const user = (state = {}, { type, user }) => {
  switch (type) {
    case AUTH_SUCCESS:
      return user;
    case LOGOUT:
    case AUTH_FAILURE:
      return {};
    default:
      return state;
  }
};

const error = (state = null, { type, error }) => {
  switch (type) {
    case AUTH_FAILURE:
      return error;
    default:
      return state;
  }
};

export default combineReducers({
  isAuthorising,
  isAuthenticated,
  user,
  error
});

// Actions
const startAuthenticating = () => ({ type: AUTH_REQUEST });
const authFailure = error => ({ type: AUTH_FAILURE, error });
const authenticated = user => ({ type: AUTH_SUCCESS, user });
const logout = () => ({ type: LOGOUT });

export const attemptLogin = () => (dispatch, getState) => {
  dispatch(startAuthenticating());
  const state = getState();

  return localforage.getItem('token').then(token => {
    const authMethod = token ? provider.isAuthenticated : provider.authenticate;
    return authMethod(token, state.config.auth)
      .then(user => {
        dispatch(authenticated(user));
        return user;
      })
      .then(user => localforage.setItem('token', user.token))
      .catch(err => {
        localforage.removeItem('token').then(() => {
          dispatch(authFailure('Not authorized'));
        });
      });
  });
};

export const checkForTokenAuth = () => (dispatch, getState) => {
  dispatch(startAuthenticating());
  const state = getState();
  return localforage.getItem('token').then(token => {
    console.log('Token', token);
    if (!token) {
      localforage.removeItem('token').then(() => {
        dispatch(authFailure('Not authorized'));
      });
    }

    return provider
      .isAuthenticated(token, state.config.auth)
      .then(user => {
        dispatch(authenticated(user));
        return user;
      })
      .then(user => localforage.setItem('token', user.token))
      .catch(err => {
        localforage.removeItem('token').then(() => {
          dispatch(authFailure('Not authorized'));
        });
      });
  });
};

export const logoutUser = () => dispatch => {
  return provider
    .logout()
    .then(() => localforage.removeItem('token'))
    .then(() => {
      dispatch(logout());
    });
};

