import React, { Component } from 'react';
import Select from '../select';
import Image from '../../image';
import { getItemsByProp } from '../../../state/collections';
import { connect } from 'react-redux';
import './style.css';

class ImageWidget extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: props.name,
      image: null,
      raw: props.raw
    };

    this.getFile = this.getFile.bind(this);
  }

  getFile(e) {
    let reader = new FileReader();
    let file = e.target.files[0];

    reader.onloadend = () => {
      this.setState({
        name: file.name,
        image: reader.result,
        raw: reader.result.replace(/^data:image\/(png|jpg|jpeg);base64,/, '')
      });
    };

    reader.readAsDataURL(file);
  }

  render() {
    const {
      id,
      value,
      required,
      allowMultiple,
      isEditing,
      default: defaultValue,
      collection,
      items
    } = this.props;

    if (isEditing) {
      return (
        <span>
          <Image
            name={value}
            id={collection}
            style={{
              height: 100
            }}
          />
          <input type="hidden" id="name" name="name" value={value} />
        </span>
      );
    }

    return (
      <div>
        <input
          onChange={this.getFile}
          id={`${id}-image`}
          name={`${id}-image`}
          type="file"
          accept="image/*"
          required={required}
        />
        {this.state.name &&
          <input type="hidden" id="name" name="name" value={this.state.name} />}
        <img
          style={{
            width: 100
          }}
          src={this.state.image}
        />
        <input type="hidden" id="raw" name="raw" value={this.state.raw} />
      </div>
    );
  }
}

export default ImageWidget;



