import React, { Component } from 'react';
import Select from '../select';
import { getItemsByProp } from '../../../state/collections';
import { connect } from 'react-redux';
import './style.css';

class Relation extends Component {
  render() {
    const {
      id,
      value,
      allowMultiple,
      default: defaultValue,
      items,
      emptyState,
      required
    } = this.props;
    return (
      <div>
        {allowMultiple &&
          <MultipleItems id={id} values={value} items={items} />}
        {!allowMultiple &&
          <Select
            id={id}
            default={value || defaultValue}
            choices={items}
            emptyState={emptyState}
            required={required}
          />}
      </div>
    );
  }
}

export default connect((state, { collection, valueKey: prop }) => ({
  items: getItemsByProp(state, collection, prop)
}))(Relation);

const MultipleItems = ({ items, id, values }) =>
  <span className="multiple-checkboxes">
    {items.map((item, i) =>
      <span className="checkbox-wrapper">
        <input
          id={`${id}${item}`}
          name={`${id}[${i}]`}
          className={'checkbox-wrapper__checkbox'}
          type="checkbox"
          value={item}
          defaultChecked={values && values.includes(item)}
        />
        <label htmlFor={`${id}${item}`} className={'checkbox-wrapper__label'}>
          {item}
        </label>
      </span>
    )}
  </span>;



