import React from 'react';
import './style.css';
import Form from '../form';
import { getCollection } from '../../state/config';
import {
  getItem,
  addItem,
  updateItem,
  removeItem
} from '../../state/collections';
import { connect } from 'react-redux';

const Collection = ({
  id,
  slug,
  collection,
  isEditing,
  addItem,
  updateItem,
  removeItem,
  data
}) => {
  const { name, fields } = collection;
  return (
    <div>
      <h1>
        {isEditing ? 'Edit' : 'Add'} {name}
      </h1>

      <div className="form">
        <Form
          collection={id}
          slug={isEditing ? slug : null}
          fields={fields}
          onSubmit={data => {
            isEditing ? updateItem(id, slug, data) : addItem(id, data);
          }}
          onDelete={removeItem}
          type={'add'}
          edit={isEditing}
          data={data}
        />
      </div>
    </div>
  );
};

export default connect(
  (state, { match }) => {
    const { id, slug } = match.params;
    return {
      id,
      slug,
      isEditing: slug,
      collection: getCollection(state, id),
      data: getItem(state, id, slug)
    };
  },
  {
    addItem,
    updateItem,
    removeItem
  }
)(Collection);



