import React from 'react';
import './style.css';

const Boolean = ({ id, value, required, default: defaultValue }) => (
  <input
    id={id}
    name={id}
    required={required}
    type="checkbox"
    defaultChecked={defaultValue}
  />
);

export default Boolean;



