// import config from '../cms/config.json';

// Constants
export const CONFIG_LOADED = "CONFIG_LOADED";

// Reducer
export default (state = null, { type, config }) => {
  switch (type) {
    case CONFIG_LOADED:
      return config;
    default:
      return state;
  }
};

const fetchConfig = url => {
  return fetch(url)
    .then(response => response.json())
    .catch(console.log);
};

// Actions
export const getConfig = (url = "config.json") => (dispatch, state) => {
  fetchConfig(url).then(config => {
    dispatch({
      type: CONFIG_LOADED,
      config,
    });
  });
};

// Selectors
export const getCollections = state =>
  state.config.collections.map(({ name, plural, id }) => ({
    id,
    name: plural || name,
    type: "collection",
  }));

export const getCollection = (state, id) =>
  state.config.collections.find(collection => collection.id === id);

export const getSingles = state =>
  state.config.singles.map(({ name, id }) => ({
    id,
    name,
    type: "single",
  }));

export const getSingle = (state, id) =>
  state.config.singles.find(single => single.id === id);



