import React, { Component } from 'react';
import { connect } from 'react-redux';
import { attemptLogin, checkForTokenAuth } from '../../state/auth';
import { load } from '../../state/collections';
import { load as loadSingle } from '../../state/singles';
import { getCollections, getSingles } from '../../state/config';
import { withRouter } from 'react-router';
import './style.css';

class AuthenticatedWrapper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hasLoadedComponents: false
    };
  }

  componentWillMount() {
    this.props.checkForTokenAuth();
  }

  componentDidUpdate() {
    const {
      isAuthenticated,
      collections,
      singles,
      load,
      loadSingle
    } = this.props;

    if (isAuthenticated && !this.state.hasLoadedComponents) {
      collections.map(collection => {
        load(collection.id);
      });

      singles.map(single => {
        loadSingle(single.id);
      });

      this.setState({
        hasLoadedComponents: true
      });
    }
  }

  render() {
    const {
      isAuthenticated,
      children,
      attemptLogin,
      error,
      isAuthorising
    } = this.props;
    return (
      <div>
        {isAuthenticated && children}
        {!isAuthenticated &&
          <div className="login-wrapper">
            <div>
              {error &&
                <p>
                  {error}
                </p>}
              <button onClick={attemptLogin} disabled={isAuthorising}>
                {isAuthorising ? 'Attempting login...' : 'Login'}
              </button>
            </div>
          </div>}
      </div>
    );
  }
}

export default withRouter(
  connect(
    state => ({
      error: state.auth.error,
      isAuthenticated: state.auth.isAuthenticated,
      isAuthorising: state.auth.isAuthorising,
      collections: getCollections(state),
      singles: getSingles(state)
    }),
    { attemptLogin, load, loadSingle, checkForTokenAuth }
  )(AuthenticatedWrapper)
);



