import React from 'react';
import './style.css';

const Select = ({
  id,
  value,
  required,
  choices = [],
  default: defaultValue,
  emptyState = false
}) =>
  <select id={id} name={id} defaultValue={defaultValue} required={required}>
    {emptyState &&
      <option value="">
        {emptyState}
      </option>}

    {choices.map(option =>
      <option key={option}>
        {option}
      </option>
    )}
  </select>;

export default Select;



