import React from 'react';
import String from '../widgets/string';
import Text from '../widgets/text';
import Boolean from '../widgets/boolean';
import Select from '../widgets/select';
import Image from '../widgets/image';
import Relation from '../widgets/relation';
import Repeater from '../widgets/repeater';
import Group from '../widgets/group';
import './style.css';

const Widget = ({
  id,
  name,
  label,
  required = false,
  value,
  widget,
  info,
  collection,
  isEditing = false,
  options = {}
}) => {
  if (widget === 'group') {
    return <Group name={name} />;
  }

  return (
    <div className="widget">
      <label className="widget__label" htmlFor={id}>
        {name}
        {required && <span>Required</span>}
      </label>
      <span className="widget__input" key={`${id}-${value}`}>
        {widget === 'string' &&
          <String id={id} value={value} required={required} {...options} />}
        {widget === 'text' &&
          <Text id={id} value={value} required={required} {...options} />}
        {widget === 'booloen' &&
          <Boolean id={id} value={value} required={required} {...options} />}
        {widget === 'select' &&
          <Select id={id} value={value} required={required} {...options} />}
        {widget === 'relation' &&
          <Relation id={id} value={value} required={required} {...options} />}
        {widget === 'repeater' &&
          <Repeater
            id={id}
            value={value}
            required={required}
            {...options}
            isEditing={isEditing}
            collection={collection}
          />}
        {widget === 'image' &&
          <Image
            id={id}
            value={value}
            required={required}
            {...options}
            isEditing={isEditing}
            collection={collection}
          />}
      </span>

      {info &&
        <small className="widget__info">
          {info}
        </small>}
    </div>
  );
};

export default Widget;



