import React, { Component } from 'react';
import Widget from '../widget';
import formToObj from 'form-data-to-object';

class Form extends Component {
  constructor(props) {
    super(props);
    this.onSubmit = this.onSubmit.bind(this);
    this.onDelete = this.onDelete.bind(this);
  }

  onSubmit(e) {
    e.preventDefault();
    const formData = new FormData(e.target);
    const data = {};
    for (var [key, value] of formData.entries()) {
      data[key] = value;
    }

    this.props.onSubmit(formToObj.toObj(data));
  }

  onDelete(e) {
    e.preventDefault();
    const { slug, collection } = this.props;
    this.props.onDelete(collection, slug);
  }

  render() {
    const { fields, collection, data = {}, edit = false } = this.props;
    return (
      <form onSubmit={this.onSubmit}>
        {fields.map((field, i) => {
          if (!field.widget) {
            return null;
          }

          return (
            <Widget
              key={`${collection}${field.id}`}
              {...field}
              value={data[field.id]}
              isEditing={edit}
              collection={collection}
            />
          );
        })}

        <button>Save</button>
        {edit &&
          <button onClick={this.onDelete} className="button--subtle">
            Delete
          </button>}
      </form>
    );
  }
}

Form.defaultProps = {
  onSubmit: () => {},
  onDelete: () => {}
};

export default Form;



