import React from 'react';
import './style.css';
import { getCollections, getSingles } from '../../state/config';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

const CollectionList = ({ items }) =>
  <ul className="collection-list">
    {items.map(({ id, name, type }) =>
      <li key={id}>
        <Link className="collection-list__item" to={`/${type}s/${id}`}>
          {name}
        </Link>
      </li>
    )}
  </ul>;

export default connect(state => {
  return {
    items: [...getCollections(state), ...getSingles(state)]
  };
}, {})(CollectionList);



