import React, { Component } from 'react';
import Select from '../select';
import { getItemsByProp } from '../../../state/collections';
import { connect } from 'react-redux';
import Widget from '../../widget';
import uuid from 'uuid';
import './style.css';

class Repeater extends Component {
  constructor(props) {
    super(props);

    console.log(props.value);
    let values = [];
    if (props.value) {
      values = props.value.map(value => ({
        id: uuid(),
        ...value
      }));
    }

    this.state = {
      items: values
    };

    this.addRow = this.addRow.bind(this);
    this.removeRow = this.removeRow.bind(this);
  }

  addRow(e) {
    e.preventDefault();
    this.setState({
      items: [...this.state.items, { id: uuid() }]
    });
  }

  removeRow(e, id) {
    e.preventDefault();
    this.setState({
      items: this.state.items.filter(item => item.id !== id)
    });
  }

  render() {
    const {
      id,
      value,
      allowMultiple,
      default: defaultValue,
      items,
      emptyState,
      fields,
      isEditing,
      collection,
      required
    } = this.props;
    return (
      <div className="repeater">
        {this.state.items.map((item, i) =>
          <div className="repeater__inner">
            {fields.map((field, j) => {
              if (!field.widget) {
                return null;
              }

              const { id: fieldId, ...rest } = field;

              return (
                <Widget
                  key={`${collection}${i}${field.id}`}
                  {...rest}
                  id={`${id}[${i}][${fieldId}]`}
                  value={item[field.id]}
                  isEditing={isEditing}
                  collection={collection}
                />
              );
            })}

            <div className="repeater__footer">
              <button
                className="repeater__remove"
                onClick={e => this.removeRow(e, item.id)}
              >
                Remove
              </button>
            </div>
          </div>
        )}
        <button className="repeater__add" onClick={this.addRow}>
          Add
        </button>
      </div>
    );
  }
}

export default Repeater;



