import React from 'react';
import collection from '../../components/collection';
import itemList from '../../components/itemList';
import './style.css';
import { Route } from 'react-router-dom';

const Collections = ({ match }) =>
  <div>
    <Route exact path={`${match.url}/:id`} component={itemList} />
    <Route path={`${match.url}/:id/add`} component={collection} />
    <Route path={`${match.url}/:id/edit/:slug`} component={collection} />
    <Route
      exact
      path={match.url}
      render={() => <h3>Please select a category.</h3>}
    />
  </div>;

export default Collections;



