import React from 'react';
import './style.css';

const String = ({ id, value, required, prefix, type = 'text' }) => (
  <span className="string-widget">
    {prefix && <span className="string-widget__prefix">{prefix}</span>}
    <input
      id={id}
      name={id}
      type={type}
      required={required}
      defaultValue={value}
    />
  </span>
);

export default String;



