import GitHub from 'github-api';

import localforage from 'localforage';
localforage.config({
  driver: localforage.LOCALSTORAGE,
  name: 'gitHub'
});

const isAuthenticated = token => {
  return new Promise((resolve, reject) => {
    const gh = new GitHub({ token });
    const user = gh.getUser();
    if (!user) {
      reject({ message: 'Token has expired' });
    }

    const repoName = 'rpullinger/schema-cms';

    user
      .listRepos()
      .then(repos => {
        const hasAccess =
          repos.data.filter(({ full_name }) => full_name === repoName).length >
          0;
        if (!hasAccess) {
          reject({ message: 'User has no access to repo' });
        }
        resolve({ token: user.__auth.token });
      })
      .catch(err => {
        reject(err);
      });
  });
};

const delay = t => {
  return new Promise(resolve => {
    setTimeout(resolve, t);
  });
};

const handleMessage = (provider, authWindow, resolve, reject, pointer) => e => {
  const { data, origin } = e;

  const states = {
    handshake: `authorizing:${provider}`,
    success: `authorization:${provider}:success:`,
    error: `authorization:${provider}:error:`
  };

  if (typeof data !== 'string') {
    return;
  }

  // Handshake
  if (data === states.handshake) {
    authWindow.postMessage(data, origin);
  }

  // Success
  if (data && data.indexOf(states.success) === 0) {
    authWindow.close();
    const data = JSON.parse(e.data.replace(states.success, ''));
    resolve(data);
    window.removeEventListener('message', pointer, false);
  }

  // Error
  if (data && data.indexOf(states.error) === 0) {
    authWindow.close();
    const data = JSON.parse(e.data.replace(states.error, ''));
    reject(data);
    window.removeEventListener('message', pointer, false);
  }
};

const authenticate = (token = false, auth) => {
  return new Promise((resolve, reject) => {
    const width = 960;
    const height = 600;
    const left = window.screen.width / 2 - width / 2;
    const top = window.screen.height / 2 - height / 2;

    const authWindow = window.open(
      `${auth.url}/auth`,
      'Authenticating',
      `toolbar=no, location=no, directories=no, status=no, menubar=no, scrollbars=no, resizable=no, copyhistory=no, width=${width}, height=${height}, top=${top} left=${left}`
    );
    let fn;
    const handler = handleMessage('github', authWindow, resolve, reject, fn);
    fn = handler;
    window.addEventListener('message', fn, false);
    authWindow.focus();
  });
};

const logout = () => Promise.resolve();

const getRepo = (repo, token) => {
  const [user, repoName] = repo.split('/');
  const gh = new GitHub({ token });
  return gh.getRepo(user, repoName);
};

const writeFile = (repo, branch, path, content, message, isBase64 = false) => {
  return repo.writeFile(
    branch,
    path,
    !isBase64 ? JSON.stringify(content, null, 2) : content,
    message,
    {
      encode: !isBase64
    }
  );
};

const loadCollection = (
  id,
  { folder },
  { repository, branch = 'master' },
  token
) => {
  const path = `${folder}/index.json`;
  const repo = getRepo(repository, token);
  return repo
    .getSha(branch, path)
    .then(response => response.data.sha)
    .then(sha => repo.getBlob(sha))
    .then(response => {
      const hasData = Object.keys(response.data).length > 0;
      return hasData ? response.data : {};
    });
};

const addItem = (
  id,
  slug,
  item,
  { folder },
  { repository, branch = 'master' },
  token
) => {
  const repo = getRepo(repository, token);
  return Promise.resolve()
    .then(() => loadCollection(id, { folder }, { repository, branch }, token))
    .then(collection => {
      const itemAll =
        id === 'media'
          ? {
              name: item.name,
              alt: item.alt
            }
          : item;

      return writeFile(
        repo,
        branch,
        `${folder}/index.json`,
        {
          ...collection,
          [slug]: itemAll
        },
        `Update ${id} index`
      );
    })
    .then(() => {
      if (id === 'media' && item.raw) {
        return delay(0).then(() =>
          writeFile(
            repo,
            branch,
            `${folder}/${item.name}`,
            item.raw,
            `Add ${slug} to ${id}`,
            true
          )
        );
      }
    });
};

const updateItem = addItem;

const removeItem = (
  id,
  slug,
  { folder },
  { repository, branch = 'master' },
  token
) => {
  const repo = getRepo(repository, token);
  return Promise.resolve()
    .then(() => loadCollection(id, { folder }, { repository, branch }, token))
    .then(collection => {
      const newIndex = {
        ...collection
      };
      delete newIndex[slug];
      return writeFile(
        repo,
        branch,
        `${folder}/index.json`,
        newIndex,
        `Update ${id} index`
      );
    })
    .then(() => {
      repo.deleteFile(branch, `${folder}/${slug}.json`);
    });
};

const loadSingle = (
  id,
  { folder },
  { repository, branch = 'master' },
  token
) => {
  const path = `${folder}/${id}.json`;
  const repo = getRepo(repository, token);
  return repo
    .getSha(branch, path)
    .then(response => response.data.sha)
    .then(sha => repo.getBlob(sha))
    .then(response => {
      const hasData = Object.keys(response.data).length > 0;
      return hasData ? response.data : {};
    });
};

const updateSingle = (
  id,
  data,
  { folder },
  { repository, branch = 'master' },
  token
) => {
  const repo = getRepo(repository, token);
  return writeFile(
    repo,
    branch,
    `${folder}/${id}.json`,
    data,
    `Update ${id} single`
  );
};

export default {
  logout,
  isAuthenticated,
  authenticate,
  loadCollection,
  addItem,
  updateItem,
  removeItem,
  loadSingle,
  updateSingle
};



