import history from '../utils/history';
import { startLoading, finishLoading, errorLoading } from './loading';
import * as config from './config';
import provider from '../providers';
import slugify from 'slugify';

// Constants
export const LOAD = 'SINGLE_LOAD';
export const UPDATE = 'SINGLE_UPDATE';

// Reducer
export default (state = {}, { type, slug, collectionID, data }) => {
  switch (type) {
    case LOAD:
    case UPDATE:
      return {
        ...state,
        [collectionID]: data
      };
    default:
      return state;
  }
};

// Action creators
const loadSingle = (collectionID, data) => ({
  type: LOAD,
  collectionID,
  data
});

const updateSingle = (collectionID, data) => ({
  type: UPDATE,
  collectionID,
  data
});

// Actions
export const load = singleID => (dispatch, getState) => {
  const state = getState();

  // Don't load if we already have the single
  if (
    state.singles[singleID] &&
    Object.keys(state.singles[singleID]).length > 0
  ) {
    return;
  }

  // Grab extra info from state
  const singleConfig = config.getSingle(state, singleID);
  const backend = state.config.backend;
  const token = state.auth.user.token;

  dispatch(startLoading());
  provider
    .loadSingle(singleID, singleConfig, backend, token)
    .then(data => {
      dispatch(loadSingle(singleID, data));
      dispatch(finishLoading());
    })
    .catch((err, response) => {
      dispatch(loadSingle(singleID, {}));
      dispatch(finishLoading());
    });
};

export const update = (singleID, data) => (dispatch, getState) => {
  const state = getState();
  const singleConfig = config.getSingle(state, singleID);
  const backend = state.config.backend;
  const token = state.auth.user.token;

  dispatch(startLoading());
  provider
    .updateSingle(singleID, data, singleConfig, backend, token)
    .then(() => {
      dispatch(updateSingle(singleID, data));
      dispatch(finishLoading());
      history.push(`/singles/${singleID}`);
    })
    .catch(err => {
      dispatch(errorLoading('An error has occurred'));
    });
};

// Selectors
export const getData = (state, singleID) => state.singles[singleID] || {};



