import { combineReducers } from 'redux';

// Constants
export const START = 'LOADING_START';
export const FINISH = 'LOADING_FINISH';
export const ERROR = 'LOADING_ERROR';

// Reducer
const visible = (state = false, { type }) => {
  switch (type) {
    case START:
      return true;
    case FINISH:
      return false;
    default:
      return state;
  }
};

const error = (state = false, { type, message }) => {
  switch (type) {
    case START:
    case FINISH:
      return false;
    case ERROR:
      return message;
    default:
      return state;
  }
};

export default combineReducers({
  visible,
  error
});

// Actions
export const startLoading = () => ({ type: START });
export const finishLoading = () => ({ type: FINISH });
export const errorLoading = message => ({ type: ERROR, message });



