import React, { Component } from 'react';
import './style.css';
import { connect } from 'react-redux';
import { getCollection } from '../../state/config';
import { load, getItems } from '../../state/collections';
import Image from '../image';
import { Link } from 'react-router-dom';

const renderField = (value, type, id) => {
  if (typeof value === 'object') {
    if (value.length > 0 && typeof value[0] === 'object') {
      return value.length;
    }

    return value.filter(item => item).join(', ');
  }

  if (type === 'image') {
    return <Image name={value} id={id} style={{ height: 100 }} />;
  }

  return value;
};

class ItemList extends Component {
  constructor(props) {
    super(props);
    this.update = this.update.bind(this);
  }

  componentDidMount() {
    this.update();
  }

  componentDidUpdate({ id }) {
    if (id !== this.props.id) {
      this.update();
    }
  }

  update() {
    const { load, id } = this.props;
    load(id);
  }

  render() {
    const { id, url, collection, items } = this.props;
    const { name, plural, listColumns = [], fields } = collection;
    const keys = Object.keys(items);
    const columns =
      listColumns.length > 0 ? listColumns : fields.map(field => field.id);
    return (
      <div>
        <h1>
          {plural || name}
        </h1>
        <p>
          <Link to={`${url}/add`}>
            Add {name}
          </Link>
        </p>
        <table className="item-table">
          <thead>
            <tr>
              {columns.map(column =>
                <th key={column}>
                  {fields.find(({ id }) => id === column).name}
                </th>
              )}
              <th> </th>
            </tr>
          </thead>
          <tbody>
            {keys.map(slug =>
              <tr key={slug}>
                {columns.map(column =>
                  <td key={column}>
                    {renderField(
                      items[slug][column],
                      fields.find(({ id }) => id === column).widget,
                      id
                    )}
                  </td>
                )}
                <td>
                  <Link
                    className="collection-list__item"
                    to={`/collections/${id}/edit/${slug}`}
                  >
                    Edit
                  </Link>
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    );
  }
}

export default connect(
  (state, { match }) => {
    const { id } = match.params;
    return {
      id,
      url: match.url,
      collection: getCollection(state, id),
      items: getItems(state, id)
    };
  },
  {
    load
  }
)(ItemList);



